.project{
  text-align: center;
  justify-self: center;
  align-self: center;
  min-height: 400px;
  width: 100%;
  /* z-index: 5; */
}
.projectTitle h2{
  font-size: 2rem;
  margin-bottom: 0;
  
}
@keyframes zoomIn {
  from {
    opacity: 0;
    -webkit-transform: scale3d(0.3, 0.3, 0.3);
    transform: scale3d(0.3, 0.3, 0.3);
  }

  50% {
    opacity: 1;
  }
}

.item{
  min-width: 260px;
  min-height: 260px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  outline: none;
  
}

.titleItem {
  align-self: center;
  background-color: skyblue;
  width: 95%;
  margin: 3px;
  height: 90px
}
.subTitle{
  margin-bottom: 30px;
  margin-top: 0;
  color: red;
}

.projectBox{
  position: relative;
  margin: 10px;
  -webkit-transition: 0.5s ease;
    -moz-transition: 0.5s ease; 
  -o-transition: 0.5s ease; 
  
}
.projectBox:hover{
  box-sizing: border-box;
  background-color: rgb(0, 0, 0);
  cursor: pointer;
}


.itemText{
  display: flex;
  align-self: center;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;


  transition: 0.5s ease-in-out;
  transition-duration: 0.5s;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
  transition: transform .5s;
  
}
/* .projectBox::before {
  content: '';
  transition: 2s ease-out;
  animation-duration: 2s;
} */

.text{
  color: white;
  font-size: 16px;
  width: 100%;
  min-width: 200px; 
  text-shadow: 1px 1px 1px black;
}
.moInfo{
  display: flex;
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 1;
}
.moInfo p {
  width: 100%;
  align-self: center;
  color: rgb(255, 255, 255);
  font-size: 20px;
  font-weight: bold;
  text-shadow: black 1px 0 5px;
  letter-spacing: 1px;
  /* -webkit-text-stroke: 1px black; */
}
.projectImg {
  width: 100%;
  height: 100%;
  position: relative;
  /* z-index: 3; */
  opacity: 1;
  transition: 0.5s ease;
  backface-visibility: hidden;
  overflow: hidden;
  box-sizing: border-box;
}

.projectBox:hover .projectImg {
  opacity: 0.4;
  box-sizing: border-box;
  transform: scale(1.5);
  overflow: hidden;

}

.projectBox:hover .itemText {
  opacity: 1;
}

.projectBox:hover .moInfo {
  transition: 0.1s ease;
  opacity: 0;
  
}

